<template>
  <div>
    <div
        class="elementor-section elementor-top-section elementor-element elementor-element-d0e3f25 elementor-section-full_width layout-pt-md layout-pb-md elementor-section-height-default elementor-section-height-default"
        data-id="d0e3f25" data-element_type="section">
      <v-container class="py-0">
        <v-row class="row-margin">
          <v-col v-for="(item,index) in items" :key="index+'companylogos'" lg="2" md="4" cols="6"
                 class="elementor-column elementor-col-16 elementor-inner-column elementor-element elementor-element-48c410e5"
                 data-id="48c410e5" data-element_type="column">
            <div class="p-0 clients elementor-widget-wrap elementor-element-populated">
              <div class="elementor-element elementor-element-2c11942b elementor-widget elementor-widget-image"
                   data-id="2c11942b" data-element_type="widget" data-widget_type="image.default">
                <div class="elementor-widget-container">
                  <v-tooltip
                      color="primary"
                      bottom
                  >
                    <template v-slot:activator="{on}">
                      <a v-on="on" :href="item.website_link" class="wrapper_css clients__image"> <img decoding="async"
                                                                                                      width="109"
                                                                                                      height="77"
                                                                                                      :src="item.image"
                                                                                                      class="attachment-full size-full wp-image-38"
                                                                                                      alt=""
                                                                                                      loading="lazy">
                      </a>
                    </template>
                    {{item.name}}
                  </v-tooltip>
                </div>
              </div>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CompanyLogos",
  created() {
    this.fetchSpanser();
  },
  data() {
    return {
      loading: false,
      items: []
    }
  },
  methods: {
    fetchSpanser() {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/sponsors').then(response => {
        this.items = response.data.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false;
      })
    }
  }
}
</script>

